<script>
export default {
  name: "applyDailyJobPermitComponent",
  data() {
    return {
      positionFixedHeight: "",
      vdrList: {},
      constructionWorkerList: [],
      dailyInsuranceData: null,
      copyForm: {},
      limitDay: "",
      projectDate: "",
      minDate: "",
      maxDate: "",
      limitMin: "",
      limitRange: "",
      defaultDayRange: "7",
      insuranceMinDate: "",
      form: {
        Apply_Pk: null,
        Draft: false,
        Project_No: "",
        Area_Pk: "",
        Area_Name: "",
        Duty_Id: "",
        Duty_Name: "",
        Duty_Tel: "",
        Safe_Id: "",
        Safe_Name: "",
        Work_Date: "",
        Work_End_Date: "",
        Work_Content: "",
        constructionDateRange: [],
        constructionDate: "",
        constructionTime: ["08:00", "18:00"],
        Begin_Time: "",
        End_Time: "",
        Duty_S_Id: "",
        Safe_S_Id: "",
        Project_Name: "",
        Eng_No: "",
        Eng_Name: "",
        Eng_Ext_No: "",
        Location_Id: "",
        Location_Name: "",
        Location_Type_Description: "",
        Supplier_Short_Name: "",
        Supplier_Tel: "",
        Floor_Id: "",
        Location_Type_Id: null,
        allWorkType: [],
        All_Work_Type: [],
        Work_Type_Normal: "0",
        normAdvocacy: [],
        workgroupName: [],
        company_Bbs_Id: "",
        company_Bbs_Id_Check: false,
        effect_Area_Type: [],
        effect_Area_Remark: "",
        SidProjectDailyInsurance: {
          insurance_no: "",
          apply_Pk: "",
          project_No: "",
          apply_Date: "",
          location_Id: "",
          location_Name: "",
          s_Id: "",
          s_Name: "",
          duty_Id: "",
          duty_Name: "",
          duty_Tel: "",
          begin_Work_Date: "",
          end_Work_Date: "",
          begin_Work_Time: "",
          end_Work_Time: "",
          break_Work_System: "",
          break_Work_Other: "",
          fire_Replace_Plan: "",
          break_Work_Explain: "",
          create_Date: null,
          create_By: null,
          emp_No: "",
          dept_Name: "",
          tel1: "",
          effect_Area_Type: [],
          effect_Area_Remark: "",
          break_Work_Reason: "",
          company_Bbs_Id: "",
          retired: "",
          retired_Date: "",
          enter: "",
          enter_Date: "",
          enter_By: "",
          retired_By: "",
        },
        Can_Group_Persons: [],
        constructionWorkers: [],
        insurance_no: "",
        estimatedWorkingTime: [],
        break_Work_System: [],
        break_Work_Other: "",
        break_Work_Other_checkbox: false,
        break_Work_Reason: "",
        fire_Replace_Plan: "",
        break_Work_Explain: "",
      },
      rules: {
        Duty_Id: [{ required: true, message: "必填", trigger: "change" }],
        Safe_Id: [{ required: false, message: "必填", trigger: "change" }],
        constructionDate: [{ required: false, trigger: "blur" }],
        constructionDateRange: [{ required: false, trigger: "blur" }],
        constructionTime: [
          { required: true, message: "必填", trigger: "blur" },
        ],
        Area_Pk: [{ required: true, message: "必填", trigger: "change" }],
        Work_Content: [{ required: true, message: "必填", trigger: "blur" }],
        Work_Type_Normal: [
          { required: true, message: "必填", trigger: "change" },
        ],
        normAdvocacy: [{ required: true, message: "必填", trigger: "change" }],
        workgroupName: [
          { required: false, message: "必填", trigger: "change" },
        ],
        constructionWorkers: [
          { required: true, message: "必填", trigger: "change" },
        ],
        allWorkType: [{ required: false, message: "必填", trigger: "change" }],
        insurance_no: [{ required: false, message: "必填", trigger: "change" }],
        estimatedWorkingTime: [
          { required: false, message: "必填", trigger: "change" },
        ],
        break_Work_System: [
          { required: false, message: "必填", trigger: "change" },
        ],
        effect_Area_Type: [
          { required: false, message: "必填", trigger: "change" },
        ],
        effect_Area_Remark: [
          { required: false, message: "必填", trigger: "change" },
        ],
        SidProjectDailyInsurance: {
          break_Work_Reason: [
            { required: false, message: "必填", trigger: "change" },
          ],
          fire_Replace_Plan: [
            { required: false, message: "必填", trigger: "change" },
          ],
          break_Work_Explain: [
            { required: false, message: "必填", trigger: "change" },
          ],
        },
        break_Work_Other: [
          { required: false, message: "必填", trigger: "blur" },
        ],
        company_Bbs_Id: [{ required: false, message: "必填", trigger: "blur" }],
        input3_1: [{ required: false, trigger: "blur" }],
        input3_2: [{ required: false, trigger: "blur" }],
        input4_1: [{ required: false, trigger: "blur" }],
        input4_2: [{ required: false, trigger: "blur" }],
        input4_3: [{ required: false, trigger: "blur" }],
        input4_4: [{ required: false, trigger: "blur" }],
        input5_1: [{ required: false, trigger: "blur" }],
        input7_1: [{ required: false, trigger: "blur" }],
        input9_1: [{ required: false, trigger: "blur" }],
        input11_1: [{ required: false, trigger: "blur" }],
      },
      pickerOptions: {
        disabledDate: (time) => {
          // 判斷資料回傳是天數還是日期
          if (this.maxDay.find((e) => e.flag === 1)) {
            this.limitDay = this.maxDay.find((e) => e.flag === 1);
          } else {
            this.limitDay = this.maxDay.find((e) => e.flag === 0);
          }

          // this.projectDate 最大工程日期
          this.projectDate = this.maxDay.find((e) => e.flag === 2).max_Day;

          const nowDay = this.formatDate(new Date());

          if (this.form.Work_Type_Normal === "1") {
            if (this.limitDay.flag === 0) {
              // const min = new Date().getTime() - 24 * 3600 * 1000 * 1;
              // 包含當天所以要-1天
              const max =
                new Date().getTime() +
                24 * 3600 * 1000 * (this.limitDay.max_Day - 1);
              // time.getTime() < min ||
              return (
                time.getTime() > max ||
                time.getTime() < new Date(nowDay).getTime() ||
                time.getTime() > new Date(this.projectDate).getTime()
              );
            } else {
              return (
                time.getTime() > new Date(this.limitDay.max_Day).getTime() ||
                time.getTime() < new Date(nowDay).getTime() ||
                time.getTime() > new Date(this.projectDate).getTime()
              );
            }
          } else {
            // 還沒有選取到第一個日期時， 最後天數先預設為工程日期
            if (!this.maxDate) {
              this.maxDate = this.projectDate;
            }

            // limitMin 選取後的日期往前推N天
            // maxDate 選取後的日期往後推N天
            if (this.limitDay.flag === 0) {
              const max =
                new Date().getTime() +
                24 * 3600 * 1000 * (this.limitDay.max_Day - 1);
              return (
                time.getTime() < this.limitMin ||
                time.getTime() > max ||
                time.getTime() > this.maxDate ||
                time.getTime() < new Date(nowDay).getTime() ||
                time.getTime() > new Date(this.projectDate).getTime()
              );
            } else {
              return (
                time.getTime() < this.limitMin ||
                time.getTime() > this.maxDate ||
                time.getTime() > new Date(this.limitDay.max_Day).getTime() ||
                time.getTime() < new Date(nowDay).getTime() ||
                time.getTime() > new Date(this.projectDate).getTime()
              );
            }
          }
        },
        onPick: ({ minDate }) => {
          // limitMin 選取後的日期往前推N天
          // maxDate 選取後的日期往後推N天
          // defaultDayRange 預設日期區間
          // limitRange 判斷區間日期是否大於預設值
          this.minDate = minDate;
          if (!this.minDate) return;
          if (this.limitDay.flag === 0) {
            if (Number(this.limitDay.max_Day) >= Number(this.defaultDayRange)) {
              this.limitRange = this.defaultDayRange;
            } else {
              this.limitRange = this.limitDay.max_Day;
            }
            this.limitMin =
              this.minDate.getTime() - (this.limitRange - 1) * 24 * 3600 * 1000;
            this.maxDate =
              minDate.getTime() + (this.limitRange - 1) * 24 * 3600 * 1000;
          } else {
            this.limitMin =
              this.minDate.getTime() -
              (this.defaultDayRange - 1) * 24 * 3600 * 1000;
            this.maxDate =
              minDate.getTime() + (this.defaultDayRange - 1) * 24 * 3600 * 1000;
          }
        },
      },
      // 限制日期顯示
      insurancePickerOptions: {
        disabledDate: (time) => {
          const nowDay = this.formatDate(new Date());
          const min = new Date(this.insuranceMinDate).getTime();
          const max =
            new Date(this.formatDate(this.insuranceMinDate)).getTime() +
            24 * 3600 * 1000 * 6;

          return (
            time.getTime() < min ||
            time.getTime() > max ||
            time.getTime() < new Date(nowDay).getTime()
          );
        },
        onPick: ({ minDate }) => {
          this.insuranceMinDate = minDate;
        },
      },
      highRiskType: [],
      dutyPeople: [],
      dailyInsurance: [],
      secondWorkArea: [],
      work_Group_List: [],
      canGroupPeople: [],
      sidSupplier: {},
      maxDay: [],
    };
  },
  computed: {
    // 高風險作業選項 & 前端資料顯示
    high_Risk_type() {
      let res = this.highRiskType.reduce((acc, cur) => {
        let index = acc.findIndex((item) => item.class_Name == cur.class_Name);
        if (index !== -1) {
          acc[index].value.push({
            class_Name: cur.class_Name,
            work_Type_Pk: cur.work_Type_Pk,
            work_Type_Name: cur.work_Type_Name,
            description: cur.description,
            approve_Item: cur.approve_Item,
            approve_She_Area: cur.approve_She_Area,
            extra_Common: cur.extra_Common,
            disable: cur.disable,
            disabled: false,
          });
        } else {
          acc.push({
            class_Name: cur.class_Name,
            value: [
              {
                class_Name: cur.class_Name,
                work_Type_Pk: cur.work_Type_Pk,
                work_Type_Name: cur.work_Type_Name,
                description: cur.description,
                approve_Item: cur.approve_Item,
                approve_She_Area: cur.approve_She_Area,
                extra_Common: cur.extra_Common,
                disable: cur.disable,
                disabled: false,
              },
            ],
          });
        }
        return acc;
      }, []);
      return res;
    },
    // 工作群組名稱選項
    workGroupList() {
      let data = [...this.work_Group_List];
      const set = new Set();
      const newArray = data.filter((item) =>
        !set.has(item.group_Name) ? set.add(item.group_Name) : false
      );
      return newArray;
    },
  },
  methods: {
    // API取得資料
    async getInit() {
      const res = await this.$store.dispatch("getInit", {
        prjNo: this.vdrList.project_No,
      });
      const data = res.data.data;
      this.highRiskType = data.high_Risk_type;
      this.dutyPeople = data.duty_People;
      this.dailyInsurance = data.daily_Insurance;
      this.secondWorkArea = data.second_Work_Area;
      this.work_Group_List = data.work_Group_List;
      this.canGroupPeople = data.can_Group_People;
      this.sidSupplier = data.sid_Supplier;
      this.maxDay = data.max_Day;
      this.canGroupPeople.forEach((e, index) => (e.id = `${e.id}-${index}`));
    },
    goBack() {
      this.$router.go(-1);
    },
    constructionDateChange() {
      this.minDate = new Date(new Date().getTime() - 24 * 3600 * 1000 * 1);
      this.limitMin = new Date(new Date().getTime() - 24 * 3600 * 1000 * 1);
      this.maxDate = this.projectDate;
    },
    breakWorkOtherHandler(evt) {
      if (evt) {
        this.rules.break_Work_Other = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("break_Work_Other");
      } else {
        this.rules.break_Work_Other = [];
        this.form.SidProjectDailyInsurance.break_Work_Other = "";
        this.$refs.form.clearValidate("break_Work_Other");
      }
    },
    breakWorkInputHandler(val) {
      if (!val) {
        this.rules.break_Work_Other = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("break_Work_Other");
      } else {
        this.rules.break_Work_Other = [];
        this.$refs.form.clearValidate("break_Work_Other");
      }
    },
    // 證號表單有異動先把規則清空
    changeLicenseInput(name) {
      this.$refs.form.clearValidate(name);
      this.rules[name] = [];
    },
    // 用value查找key
    getKeyByValue(object, value) {
      return Object.keys(object).filter((key) => object[key] === value);
    },
    // API檢查證號
    async getChkLicense(data) {
      try {
        const res = await this.$store.dispatch("chkLicense", data);
        if (!res.data.isOK) {
          const model = Object.keys(this.$refs.form.model)
            .filter((key) => key.includes("input"))
            .reduce((cur, key) => {
              return Object.assign(cur, { [key]: this.$refs.form.model[key] });
            }, {});
          // API回來的值來查找表單key
          String(res.data.data)
            .split(",")
            .forEach((e) => {
              const inputName = this.getKeyByValue(model, e);
              inputName.forEach((e) => {
                // 重新給表單驗證
                this.rules[e] = [
                  {
                    validator: function (rule, value, callback) {
                      callback(new Error("證號錯誤"));
                    },
                    trigger: "blur",
                  },
                ];
                this.$refs.form.validateField(e);
              });
            });
          return false;
        } else {
          return true;
        }
      } catch (err) {
        console.log(err);
      }
    },
    // 一般作業與高風險作業功能判斷
    workTypeNormalHandler() {
      this.$refs.form.clearValidate("Work_Type_Normal");
      if (this.form.Work_Type_Normal === "0") {
        this.rules.constructionDateRange = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.constructionDate = [{ required: false, trigger: "blur" }];
        this.form.constructionDate = "";
        this.form.All_Work_Type = [];
        this.rules.allWorkType = [{ required: false, trigger: "change" }];
      } else {
        this.rules.constructionDate = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.constructionDateRange = [
          { required: false, trigger: "blur" },
        ];
        this.form.constructionDateRange = "";
        this.rules.allWorkType = [
          { required: true, message: "必填", trigger: "change" },
        ];
      }
    },
    // 必填規則變更
    companyBbsIdHandler() {
      if (!this.form.company_Bbs_Id_Check) {
        this.form.company_Bbs_Id = "";
        this.rules.company_Bbs_Id = [];
        this.$refs.form.clearValidate("company_Bbs_Id");
      } else {
        this.rules.company_Bbs_Id = [
          { required: true, message: "必填", trigger: "change" },
        ];
        this.$refs.form.validateField("company_Bbs_Id");
      }
    },
    // 勾選施工群組裡面的工作人員
    workGroupNameHandler(evt, item) {
      if (evt) {
        const ids = item.ids;
        this.canGroupPeople.forEach((e) => {
          if (ids.indexOf(e.id.split("-")[0]) !== -1) {
            this.form.constructionWorkers.push(e.id);
          }
        });
      }
    },
    insuranceNoHandler(val) {
      const res = this.dailyInsuranceData.find((e) => e.insurance_no === val);
      this.form.SidProjectDailyInsurance = res;
      this.$set(this.form.estimatedWorkingTime, [0], res.begin_Work_Time);
      this.$set(this.form.estimatedWorkingTime, [1], res.end_Work_Time);
      this.form.break_Work_System = res.break_Work_System.split(",");
      this.form.effect_Area_Type = res.effect_Area_Type.split(",");
      if (res.break_Work_Other) {
        this.form.break_Work_Other_checkbox = true;
      }
      if (res.company_Bbs_Id) {
        this.form.company_Bbs_Id_Check = true;
      }
    },
    // 高風險表單驗證判斷
    highRiskTypeChange(item, evt) {
      // 消防單中斷申請
      if (item.work_Type_Pk === "200310150003" && evt) {
        this.rules.SidProjectDailyInsurance.break_Work_Reason = [
          { required: true, message: "必填", trigger: "change" },
        ];
        this.rules.SidProjectDailyInsurance.fire_Replace_Plan = [
          { required: true, message: "必填", trigger: "change" },
        ];
        this.rules.SidProjectDailyInsurance.break_Work_Explain = [
          { required: true, message: "必填", trigger: "change" },
        ];
        this.rules.effect_Area_Type = [
          { required: true, message: "必填", trigger: "change" },
        ];
        const data = [...this.dailyInsurance];
        const beginDateTime = new Date(this.form.constructionDate).getTime();
        const endDateTime = new Date(this.form.constructionDate).getTime();
        const sId = this.vdrList.s_Id;
        const dutyId = this.form.Duty_Id.split("-")[0];
        // 依照 廠商施工負責人 / 安衛人員的Duty_Id, s_Id, 施工日期區間 篩選出來的消防單中斷申請
        this.dailyInsuranceData = data.filter(
          (e) =>
            new Date(e.begin_Work_Date).getTime() <= beginDateTime &&
            new Date(e.end_Work_Date).getTime() >= endDateTime &&
            e.duty_Id === dutyId &&
            e.s_Id === sId
        );
      } else {
        this.rules.company_Bbs_Id = [];
        this.rules.effect_Area_Type = [];
        this.rules.insurance_no = [];
        this.rules.SidProjectDailyInsurance.break_Work_Reason = [
          { required: false, trigger: "blur" },
        ];
        this.rules.SidProjectDailyInsurance.fire_Replace_Plan = [
          { required: false, trigger: "blur" },
        ];
        this.rules.SidProjectDailyInsurance.break_Work_Explain = [
          { required: false, trigger: "blur" },
        ];
      }

      if (item.work_Type_Pk === "201103160001" && evt) {
        this.rules.input3_1 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("input3_1");
      } else if (item.work_Type_Pk === "201103160001" && !evt) {
        this.rules.input3_1 = [{ required: false, trigger: "blur" }];
        this.$refs.form.clearValidate("input3_1");
        this.form.input3_1 = "";
      } else if (item.work_Type_Pk === "202104200001" && evt) {
        this.rules.input3_2 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("input3_2");
      } else if (item.work_Type_Pk === "202104200001" && !evt) {
        this.rules.input3_2 = [{ required: false, trigger: "blur" }];
        this.$refs.form.clearValidate("input3_2");
        this.form.input3_2 = "";
      } else if (item.work_Type_Pk === "200310150005" && evt) {
        this.rules.input4_1 = [{ required: false, trigger: "blur" }];
        this.rules.input4_2 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.input4_3 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.rules.input4_4 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("input4_1");
        this.$refs.form.validateField("input4_2");
        this.$refs.form.validateField("input4_3");
        this.$refs.form.validateField("input4_4");
      } else if (item.work_Type_Pk === "200310150005" && !evt) {
        this.rules.input4_1 = [{ required: false, trigger: "blur" }];
        this.rules.input4_2 = [{ required: false, trigger: "blur" }];
        this.rules.input4_3 = [{ required: false, trigger: "blur" }];
        this.rules.input4_4 = [{ required: false, trigger: "blur" }];
        this.$refs.form.clearValidate("input4_1");
        this.$refs.form.clearValidate("input4_2");
        this.$refs.form.clearValidate("input4_3");
        this.$refs.form.clearValidate("input4_4");
        this.form.input4_1 = "";
        this.form.input4_2 = "";
        this.form.input4_3 = "";
        this.form.input4_4 = "";
      } else if (item.work_Type_Pk === "200310150001" && evt) {
        this.rules.input5_1 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("input5_1");
      } else if (item.work_Type_Pk === "200310150001" && !evt) {
        this.rules.input5_1 = [{ required: false, trigger: "blur" }];
        this.$refs.form.clearValidate("input5_1");
        this.form.input5_1 = "";
      } else if (item.work_Type_Pk === "200801090001" && evt) {
        this.rules.input7_1 = [{ required: false, trigger: "blur" }];
        this.$refs.form.validateField("input7_1");
        this.$message({
          type: "warning",
          duration: 5000,
          showClose: true,
          message:
            "您已申請interlock by pass，此作業具嚴重危害性，請提供「變更管制查核表」及「interlock By pass施工單申請紀錄」予INNOLUX發包工程單位，由發包工程師提出「安全防護裝置變更申請」，經核准後進行作業。",
        });
      } else if (item.work_Type_Pk === "200801090001" && !evt) {
        this.rules.input7_1 = [{ required: false, trigger: "blur" }];
        this.$refs.form.clearValidate("input7_1");
        this.form.input7_1 = "";
      } else if (item.work_Type_Pk === "200310150006" && evt) {
        this.rules.input9_1 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("input9_1");
      } else if (item.work_Type_Pk === "200310150006" && !evt) {
        this.rules.input9_1 = [{ required: false, trigger: "blur" }];
        this.$refs.form.clearValidate("input9_1");
        this.form.input9_1 = "";
      } else if (item.work_Type_Pk === "200403220001" && evt) {
        this.rules.input11_1 = [
          { required: true, message: "必填", trigger: "blur" },
        ];
        this.$refs.form.validateField("input11_1");
      } else if (item.work_Type_Pk === "200403220001" && !evt) {
        this.rules.input11_1 = [{ required: false, trigger: "blur" }];
        this.$refs.form.clearValidate("input11_1");
        this.form.input11_1 = "";
      } else if (
        (item.work_Type_Pk === "200310010002" && evt) ||
        (item.work_Type_Pk === "201005250001" && evt)
      ) {
        this.$message({
          type: "warning",
          duration: 5000,
          showClose: true,
          message: "請考慮是否需加選[警報偵測訊號隔離]",
        });
      }
    },
    // 取得DutyName
    getDutyName(id) {
      if (!id) return;
      const name = this.dutyPeople.find((e) => e.id === id).p_Name;
      return name;
    },
    // 取得DutyTel
    getDutyTel(id) {
      if (!id) return;
      const tel = this.dutyPeople.find((e) => e.id === id).tel1;
      return tel;
    },
    // 取得SafeName
    getSafeName(id) {
      if (!id) return;
      const name = this.dutyPeople.find((e) => e.id === id).p_Name;
      return name;
    },
    // 取得AreaName
    getAreaName(id) {
      if (!id) return;
      const description = this.secondWorkArea.find(
        (e) => e.id === id
      ).description;
      return description;
    },
    getSid(id) {
      if (!id) return;
      const sId = this.dutyPeople.find((e) => e.id === id).s_Id;
      return sId;
    },
    getWorkTypeName(pk) {
      if (!pk) return;
      const name = this.highRiskType.find(
        (e) => e.work_Type_Pk === pk
      ).work_Type_Name;
      return name;
    },
    getWorkTypeDescription(pk) {
      if (!pk) return;
      const description = this.highRiskType.find(
        (e) => e.work_Type_Pk === pk
      ).description;
      return description;
    },
    getWorkTypeClassName(pk) {
      if (!pk) return;
      const class_Name = this.highRiskType.find(
        (e) => e.work_Type_Pk === pk
      ).class_Name;
      return class_Name;
    },
    getWorkTypeApproveSheArea(pk) {
      if (!pk) return;
      const approveSheArea = this.highRiskType.find(
        (e) => e.work_Type_Pk === pk
      ).approve_She_Area;
      return approveSheArea;
    },
    getWorkTypeApproveItem(pk) {
      if (!pk) return;
      const approveItem = this.highRiskType.find(
        (e) => e.work_Type_Pk === pk
      ).approve_Item;
      return approveItem;
    },
    getGroupPeopleCourseId(id) {
      const course_Id = this.canGroupPeople.find((e) => e.id === id).course_Id;
      return course_Id;
    },
    getGroupPeopleGroupName(id) {
      const group_Name = this.canGroupPeople.find(
        (e) => e.id === id
      ).group_Name;
      return group_Name;
    },
    getGroupPeopleId(id) {
      const ID = this.canGroupPeople.find((e) => e.id === id).id;
      return ID.split("-")[0];
    },
    getGroupPeoplePName(id) {
      const p_Name = this.canGroupPeople.find((e) => e.id === id).p_Name;
      return p_Name;
    },
    getGroupPeopleSId(id) {
      let s_Id = this.canGroupPeople.find((e) => e.id === id).s_Id;
      return s_Id;
    },
    getGroupPeopleState(id) {
      const state = this.canGroupPeople.find((e) => e.id === id).state;
      return state;
    },
    // 如果有勾選高風險作業，把值塞進copyForm裡的All_Work_Type
    allWorkTypeHandler() {
      if (this.form.allWorkType.length) {
        this.form.All_Work_Type = [];
        this.form.allWorkType.forEach((e) => {
          const allWorkTypeObj = {
            class_Name: this.getWorkTypeClassName(e),
            work_Type_Pk: e,
            work_Type_Name: this.getWorkTypeName(e),
            description: this.getWorkTypeDescription(e),
            approve_Item: this.getWorkTypeApproveItem(e),
            approve_She_Area: this.getWorkTypeApproveSheArea(e),
            extra_Common: "",
          };
          this.copyForm.All_Work_Type.push(allWorkTypeObj);
          const set = new Set();
          this.copyForm.All_Work_Type = this.copyForm.All_Work_Type.filter(
            (item) =>
              !set.has(item.work_Type_Pk) ? set.add(item.work_Type_Pk) : false
          );
          // 沒有消防中斷單時送Null
          if (e !== "200310150003") {
            this.copyForm.SidProjectDailyInsurance = null;
          }
        });
      } else {
        // 沒有高風險時 消防中斷系統送null
        this.copyForm.SidProjectDailyInsurance = null;
      }
    },
    // 目前沒有用到可以刪掉
    input3SetDraftValue(draftData) {
      draftData.All_Work_Type.forEach((e) => {
        if (e.work_Type_Pk === "201103160001") {
          this.form.input3_1 = e.extra_Common;
        }
        if (e.work_Type_Pk === "202104200001") {
          this.form.input3_2 = e.extra_Common;
        }
      });
    },
    // 目前沒有用到可以刪掉
    input4SetDraftValue(draftData) {
      draftData.All_Work_Type.forEach((e) => {
        if (e.work_Type_Pk === "200310150005") {
          this.form.input4_1 = e.extra_Common.split(";")[0];
          this.form.input4_2 = e.extra_Common.split(";")[1];
          this.form.input4_3 = e.extra_Common.split(";")[2];
          this.form.input4_4 = e.extra_Common.split(";")[3];
        }
      });
    },
    // 目前沒有用到可以刪掉
    input5SetDraftValue(draftData) {
      draftData.All_Work_Type.forEach((e) => {
        if (e.work_Type_Pk === "200310150001") {
          this.form.input5_1 = e.extra_Common;
        }
      });
    },
    // 目前沒有用到可以刪掉
    input7SetDraftValue(draftData) {
      draftData.All_Work_Type.forEach((e) => {
        if (e.work_Type_Pk === "200801090001") {
          this.form.input7_1 = e.extra_Common;
        }
      });
    },
    // 目前沒有用到可以刪掉
    input9SetDraftValue(draftData) {
      draftData.All_Work_Type.forEach((e) => {
        if (e.work_Type_Pk === "200310150006") {
          this.form.input9_1 = e.extra_Common;
        }
      });
    },
    // 目前沒有用到可以刪掉
    input11SetDraftValue(draftData) {
      draftData.All_Work_Type.forEach((e) => {
        if (e.work_Type_Pk === "200403220001") {
          this.form.input11_1 = e.extra_Common;
        }
      });
    },
    // 表單資料塞值
    input3SetValue() {
      if (this.copyForm.input3_1) {
        this.copyForm.All_Work_Type.forEach((e) => {
          if (e.work_Type_Pk === "201103160001") {
            e.extra_Common = this.copyForm.input3_1;
          }
        });
      }

      if (this.copyForm.input3_2) {
        this.copyForm.All_Work_Type.forEach((e) => {
          if (e.work_Type_Pk === "202104200001") {
            e.extra_Common = this.copyForm.input3_2;
          }
        });
      }
    },
    // 表單資料塞值
    input4SetValue() {
      if (this.copyForm.input4_1) {
        this.copyForm.All_Work_Type.forEach((e) => {
          if (e.work_Type_Pk === "200310150005") {
            e.extra_Common = `${this.copyForm.input4_1};${this.copyForm.input4_2};${this.copyForm.input4_3};${this.copyForm.input4_4}`;
          }
        });
      }
    },
    // 表單資料塞值
    input5SetValue() {
      if (this.copyForm.input5_1) {
        this.copyForm.All_Work_Type.forEach((e) => {
          if (e.work_Type_Pk === "200310150001") {
            e.extra_Common = this.copyForm.input5_1;
          }
        });
      }
    },
    // 表單資料塞值
    input7SetValue() {
      if (this.copyForm.input7_1) {
        this.copyForm.All_Work_Type.forEach((e) => {
          if (e.work_Type_Pk === "200801090001") {
            e.extra_Common = this.copyForm.input7_1;
          }
        });
      }
    },
    // 表單資料塞值
    input9SetValue() {
      if (this.copyForm.input9_1) {
        this.copyForm.All_Work_Type.forEach((e) => {
          if (e.work_Type_Pk === "200310150006") {
            e.extra_Common = this.copyForm.input9_1;
          }
        });
      }
    },
    // 表單資料塞值
    input11SetValue() {
      if (this.copyForm.input11_1) {
        this.copyForm.All_Work_Type.forEach((e) => {
          if (e.work_Type_Pk === "200403220001") {
            e.extra_Common = this.copyForm.input11_1;
          }
        });
      }
    },
    // 資料塞值
    dutySetValue() {
      if (this.copyForm.Duty_Id) {
        this.copyForm.Duty_Name = this.getDutyName(
          this.copyForm.Duty_Id.split("-")[0]
        );
        this.copyForm.Duty_Tel = this.getDutyTel(
          this.copyForm.Duty_Id.split("-")[0]
        );
        this.copyForm.Duty_S_Id = this.getSid(
          this.copyForm.Duty_Id.split("-")[0]
        );
      }
    },
    // 資料塞值
    safeSetValue() {
      if (this.copyForm.Safe_Id) {
        this.copyForm.Safe_Name = this.getSafeName(
          this.copyForm.Safe_Id.split("-")[0]
        );
        this.copyForm.Safe_S_Id = this.getSid(
          this.copyForm.Safe_Id.split("-")[0]
        );
      }
    },
    // 資料塞值
    areaPkSetValue() {
      if (this.copyForm.Area_Pk) {
        this.copyForm.Area_Name = this.getAreaName(this.copyForm.Area_Pk);
      }
    },
    // 消防中斷塞值
    theFireSet() {
      if (this.copyForm.Work_Type_Normal === "0") {
        // 把消防中斷資料格式設為null
        this.copyForm.SidProjectDailyInsurance = null;
        this.copyForm.Work_Date = this.copyForm.constructionDateRange[0];
        this.copyForm.Work_End_Date = this.copyForm.constructionDateRange[1];
      } else {
        // 有選中消防系統中斷申請
        const highWorkType = this.copyForm.allWorkType;
        if (
          highWorkType.length &&
          highWorkType.indexOf("200310150003") !== -1
        ) {
          this.copyForm.SidProjectDailyInsurance =
            this.form.SidProjectDailyInsurance;
          this.copyForm.SidProjectDailyInsurance.insurance_no =
            this.form.insurance_no;
          this.copyForm.SidProjectDailyInsurance.effect_Area_Remark =
            this.form.effect_Area_Remark;
          this.copyForm.SidProjectDailyInsurance.duty_Id =
            this.copyForm.Duty_Id.split("-")[0];
          this.copyForm.SidProjectDailyInsurance.duty_Name = this.getDutyName(
            this.copyForm.Duty_Id.split("-")[0]
          );
          this.copyForm.SidProjectDailyInsurance.duty_Tel = this.getDutyTel(
            this.copyForm.Duty_Id.split("-")[0]
          );

          // 如果有預計作業
          if (this.copyForm.estimatedWorkingTime) {
            this.copyForm.SidProjectDailyInsurance.begin_Work_Date =
              this.copyForm.estimatedWorkingTime[0];
            this.copyForm.SidProjectDailyInsurance.begin_Work_Time =
              this.copyForm.estimatedWorkingTime[0];
            this.copyForm.SidProjectDailyInsurance.end_Work_Date =
              this.copyForm.estimatedWorkingTime[1];
            this.copyForm.SidProjectDailyInsurance.end_Work_Time =
              this.copyForm.estimatedWorkingTime[1];
          }
          this.copyForm.SidProjectDailyInsurance.company_Bbs_Id =
            this.copyForm.company_Bbs_Id;
          if (this.copyForm.break_Work_System.length) {
            this.copyForm.SidProjectDailyInsurance.break_Work_System =
              this.copyForm.break_Work_System.join(",");
          }
          if (this.copyForm.effect_Area_Type.length) {
            this.copyForm.SidProjectDailyInsurance.effect_Area_Type =
              this.copyForm.effect_Area_Type.join(",");
          }
        }

        this.copyForm.Work_Date = this.copyForm.constructionDate;
        this.copyForm.Work_End_Date = this.copyForm.constructionDate;
      }
    },
    // 資料塞值
    constructionTimeSet() {
      if (this.copyForm.constructionTime.length) {
        this.copyForm.Begin_Time = this.copyForm.constructionTime[0];
        this.copyForm.End_Time = this.copyForm.constructionTime[1];
      }
    },
    // 施工人員塞值
    constructionWorkersSet() {
      if (this.copyForm.constructionWorkers.length) {
        this.copyForm.Can_Group_Persons = [];
        this.copyForm.constructionWorkers.forEach((e) => {
          // 這邊的e 是施工人員的id
          let canGroupPersonsObj = {
            id: this.getGroupPeopleId(e),
            s_Id: this.getGroupPeopleSId(e),
            group_Name: this.getGroupPeopleGroupName(e),
            p_Name: this.getGroupPeoplePName(e),
            state: this.getGroupPeopleState(e),
            course_Id: this.getGroupPeopleCourseId(e),
          };
          this.copyForm.Can_Group_Persons.push(canGroupPersonsObj);
        });
      }
    },
    // 目前沒有用到
    fireWorkRules(data) {
      if (data.All_Work_Type.length) {
        const res = data.All_Work_Type.some(
          (e) => e.work_Type_Pk === "200310150003"
        );
        if (res) {
          if (data.SidProjectDailyInsurance.company_Bbs_Id) {
            this.rules.company_Bbs_Id = [
              { required: true, message: "必填", trigger: "blur" },
            ];
          }
          this.rules.effect_Area_Type = [
            { required: true, message: "必填", trigger: "change" },
          ];
          this.rules.insurance_no = [
            { required: true, message: "必填", trigger: "blur" },
          ];
        }
      }
    },
    // 表單對應值塞入 以及額外處理
    setForm() {
      this.copyForm = Object.assign({}, this.form);
      this.copyForm.Project_No = this.vdrList.project_No;
      this.copyForm.Project_Name = this.vdrList.project_Name;
      this.copyForm.Eng_No = this.vdrList.emp_No;
      this.copyForm.Eng_Name = this.vdrList.emp_Name;
      this.copyForm.Eng_Ext_No = this.vdrList.emp_Extension_No;
      this.copyForm.Location_Id = this.vdrList.lid;
      this.copyForm.Location_Name = this.vdrList.ld2;
      this.copyForm.Location_Type_Description = this.vdrList.ld4;
      this.copyForm.Supplier_Short_Name = this.sidSupplier.short_Name;
      this.copyForm.Supplier_Tel = this.sidSupplier.tel;
      this.copyForm.Floor_Id = this.vdrList.fab_Id;
      // 另外塞值
      this.copyForm.Floor_Id = this.vdrList.fab_Id;
      let newData = [];
      this.form.constructionWorkers.forEach((e) => {
        newData.push(e.split("-")[0]);
      });
      // this.copyForm.constructionWorkers = newData;
      if (this.copyForm.Work_Type_Normal === "1") {
        // 高風險作業區
        this.allWorkTypeHandler();
        this.input3SetValue();
        this.input4SetValue();
        this.input5SetValue();
        this.input7SetValue();
        this.input9SetValue();
        this.input11SetValue();
      } else {
        this.copyForm.All_Work_Type = [];
      }
      // 消防中斷
      this.theFireSet();
      this.dutySetValue();
      this.safeSetValue();
      this.areaPkSetValue();
      // 施工時間
      this.constructionTimeSet();
      // 施工人員
      this.constructionWorkersSet();
      // console.log("copyForm: ", this.copyForm);
    },
    submitForm(formName) {
      this.setForm();
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          try {
            const ids = [];
            let sIds = ["x"];
            // 證號表單值塞進去
            let inputs = [];
            inputs.push(
              this.form.input3_1,
              this.form.input3_2,
              this.form.input4_1,
              this.form.input4_2,
              this.form.input4_3,
              this.form.input4_4,
              this.form.input5_1,
              this.form.input7_1,
              this.form.input9_1,
              this.form.input11_1
            );
            this.form.constructionWorkers.forEach((e) => {
              this.canGroupPeople.forEach((i) => {
                if (e === i.id) {
                  sIds.push(i.s_Id);
                }
              });
              ids.push(e.split("-")[0]);
            });
            inputs = inputs.filter((e) => e !== undefined && e !== "");
            sIds.push(this.vdrList.s_Id); // 舊有的也先傳進去
            const data = {
              d: ids,
              d2: sIds.toString(), // 20240321 this.vdrList.s_Id更改為 sIds
              d3: inputs,
            };
            const model = Object.keys(this.$refs.form.model)
              .filter((key) => key.includes("input"))
              .reduce((cur, key) => {
                return Object.assign(cur, {
                  [key]: this.$refs.form.model[key],
                });
              }, {});
            // 表單送出前先把驗證規則清掉
            Object.keys(model).forEach((e) => this.changeLicenseInput(e));
            //2024.02.27 排除第一筆證號(吊車合格證號)
            var chkdata = JSON.parse(JSON.stringify(data));
            const res = this.copyForm.All_Work_Type.some(
              (e) =>
                e.work_Type_Pk === "200310150005" ||
                e.work_Type_Pk === "200801090001"
            );
            if (res) {
              let inputs = [];
              inputs.push(
                this.form.input3_1,
                this.form.input3_2,
                this.form.input4_2,
                this.form.input4_3,
                this.form.input4_4,
                this.form.input5_1,
                this.form.input9_1,
                this.form.input11_1
              );
              inputs = inputs.filter((e) => e !== undefined && e !== "");
              chkdata.d3 = inputs;
            }
            let isChk = true;
            // 一般工種不用檢查證號
            if (this.form.Work_Type_Normal === "1") {
              isChk = await this.getChkLicense(chkdata, "");
            }
            if (isChk) {
              this.copyForm.Duty_Id = this.copyForm.Duty_Id.split("-")[0];
              this.copyForm.Safe_Id = this.copyForm.Safe_Id.split("-")[0];
              this.copyForm.Can_Group_Persons.forEach(
                (e) => (e.id = e.id.split("-")[0])
              );
              const res = await this.$store.dispatch("insData", this.copyForm);
              if (res.data.isOK) {
                this.$message({
                  type: "success",
                  duration: 5000,
                  showClose: true,
                  message: "申請成功",
                });
                this.$router.push({ name: "dailyJobApplication" });
              }
            } else {
              this.$message({
                type: "error",
                duration: 5000,
                showClose: true,
                message: `證號有誤`,
              });
            }
          } catch (err) {
            console.log(err);
          }
        } else {
          this.$message({
            type: "error",
            duration: 5000,
            showClose: true,
            message: "資料填寫錯誤",
          });
        }
      });
    },
    // 消防中斷單塞值
    groupFormDailyInsurance() {
      if (this.dailyInsurance.length) return;
      const insuranceObj = {
        insurance_no: "",
        apply_Pk: null,
        project_No: this.vdrList.project_No,
        apply_Date: null,
        location_Id: this.vdrList.lid,
        location_Name: this.vdrList.ld2,
        s_Id: this.sidSupplier.s_Id,
        s_Name: this.sidSupplier.s_Name,
        duty_Id: "",
        duty_Name: "",
        duty_Tel: "",
        begin_Work_Date: "",
        end_Work_Date: "",
        begin_Work_Time: "",
        end_Work_Time: "",
        break_Work_System: "",
        break_Work_Other: "",
        fire_Replace_Plan: "",
        break_Work_Explain: "",
        create_Date: null,
        create_By: null,
        emp_No: null,
        dept_Name: null,
        tel1: null,
        effect_Area_Type: [],
        effect_Area_Remark: "",
        break_Work_Reason: "",
        company_Bbs_Id: "",
        retired: null,
        retired_Date: null,
        enter: null,
        enter_Date: null,
        enter_By: null,
        retired_By: null,
      };
      this.form.SidProjectDailyInsurance = insuranceObj;
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
  },
  async mounted() {
    this.vdrList = JSON.parse(this.$route.params.obj);
    await this.getInit();
    this.groupFormDailyInsurance();

    this.$store.commit("HEADER_HANDLER", true);
    this.$store.commit("BREADCRUMB_HANDLER", true);
    this.$root.$children[0].sidebarSwitch = true;

    const fixedFooter = document.querySelector(".position-fixed");
    const fixedFooterHeight = fixedFooter.offsetHeight;
    this.positionFixedHeight = `${fixedFooterHeight}px`;

    // console.log(this.maxDay);
  },
};
</script>

<template src="./applyDailyJobPermit.html"></template>
<style lang="sass" scoped src="../../../../public/styles/custom.sass"></style>
